import { TypeOfObjects } from "./../types/TypeOfObjects";
import { Objects } from "@/types/Objects";
import axios from "axios";

const getObjects = (typeOfObjects: TypeOfObjects) => {
  const URL =
    typeOfObjects === "I"
      ? "https://raw.githubusercontent.com/regnumtim/objects/main/objects.json"
      : "https://raw.githubusercontent.com/regnumtim/objects/main/apartments.json";
  return axios
    .get(URL)
    .then((response) => {
      const oldObjects = localStorage.getItem("objects"); //    localStorage,     objects  undefined
      const newObjects = JSON.stringify(response.data); //  JSON  objects

      if (oldObjects !== newObjects) {
        //       ,      
        localStorage.setItem("objects", JSON.stringify(response.data, null, 0));
        window.location.reload();
      } else {
        //  ,      objects
        return JSON.parse(newObjects) as Objects;
      }
    })
    .catch((error) => console.error(error));
};

export default getObjects;
